<template>
  <div id="newTaskDetails">
    <div id="newTaskHeader">
      <div id="newTaskTitle" class="formInput formItem newTaskItem">
        <label for="titleInput">
          Tytuł <span class="formError">{{ errors.titleError }}</span>
        </label>
        <div :class="titleClass" class="border">
          <input
            name="titleInput"
            list="text"
            v-model="titleInput"
            maxlength="20"
            autocomplete="off"
          />
          <div class="helpBox" title="Wpisz tytuł">
            <svg-icon
              :class="titleClass"
              type="mdi"
              :path="titleIcon"
            ></svg-icon>
          </div>
        </div>
      </div>
      <div id="newTaskButtonsContainer">
        <div
          title="Ustaw ważność zadania"
          class="newTaskButton toggleStar pointer"
          :class="{ disabled: !isImportant }"
          @click="isImportant = !isImportant"
        >
          <svg-icon type="mdi" :path="iconsStore.mdiStar"></svg-icon>
        </div>
        <div
          title="Dodaj nowe zadanie"
          class="newTaskButton newTask pointer"
          :class="{ disabled: isButtonDisabled }"
          @click="addTask()"
        >
          <svg-icon type="mdi" :path="iconsStore.mdiNotePlusOutline"></svg-icon>
        </div>
      </div>
    </div>
    <div id="newTaskDesc" class="formInput formItem newTaskItem">
      <label for="descInput">Opis</label>
      <div class="border">
        <textarea rows="5" maxlength="250" v-model="descInput"></textarea>
        <span class="letterCount">
          {{ `${lettersCount.descLetters}/${lettersCount.maxDescLetters}` }}
        </span>
      </div>
    </div>
    <div id="newTaskFooter">
      <div class="newTaskTile">
        <h1>Stworzone przez</h1>
        <div id="newTaskAuthor">
          <AuthorCard :authorObject="author" />
        </div>
      </div>
      <div class="newTaskTile">
        <h1>Status</h1>
        <div id="newTaskStatus" class="statusBadge available">
          <div>
            <p>Dostępne</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import svgIcon from "@jamescoyle/vue-icon";
import popupSound from "../../assets/audio/popup.wav";

import { useAlertsStore } from "../../stores/alertsStore";
import { useAccountStore } from "../../stores/accountStore";
import { usePreferenceStore } from "../../stores/preferenceStore";
import { useIconsStore } from "../../stores/iconsStore";
import { useGeneralStore } from "../../stores/generalStore";

import AuthorCard from "../../components/Assets/AuthorCard.vue";

export default {
  name: "AddTask",
  components: {
    svgIcon,
    AuthorCard,
  },
  setup() {
    const accountStore = useAccountStore();
    const alertsStore = useAlertsStore();
    const preferenceStore = usePreferenceStore();
    const iconsStore = useIconsStore();
    const generalStore = useGeneralStore();

    return {
      accountStore,
      alertsStore,
      iconsStore,
      preferenceStore,
      generalStore,
    };
  },
  data() {
    return {
      author: {
        nick: this.accountStore.userNickname,
        role: this.accountStore.userRoles[0],
      },
      titleIcon: this.iconsStore.questionMarkIcon,
      descIcon: this.iconsStore.questionMarkIcon,
      titleInput: "",
      descInput: "",
      titleClass: "",
      isButtonDisabled: true,
      isImportant: false,
      errors: {
        titleError: "",
      },
      lettersCount: {
        descLetters: 0,
        maxDescLetters: 250,
      },
    };
  },
  methods: {
    async addTask() {
      if (this.titleInput.length < 3) return;

      const formValues = {
        title: this.titleInput,
        desc: this.descInput,
        status: "available",
        important: this.isImportant,
        createdBy: this.accountStore.userID,
      };

      await this.generalStore
        .addTask(formValues)
        .then(async (response) => {
          await this.alertsStore.showNotification(
            response.duration,
            response.status,
            response.message
          );

          if (response.status === "success") {
            this.titleInput = "";
            this.descInput = "";
            this.important = false;
            this.isButtonDisabled = true;

            await this.$router.push(`/dashboard/zadania/${response.content}`);

            this.generalStore.activeTaskElement = "home";
          }
        })
        .catch((error) => console.log(error));

      if (this.preferenceStore.preferences[0].state) {
        const notificationSound = new Audio(popupSound);
        notificationSound.volume = 0.5;
        notificationSound.play();
      }
    },
    formVerification(value) {
      if (value.length >= 3)
        return {
          icon: this.iconsStore.successIcon,
          status: "success",
          error: "",
        };
      else if (value.length > 0 && value.length < 3)
        return {
          icon: this.iconsStore.alertCircleIcon,
          status: "alert",
          error: "Minimum 3 znaki!",
        };
      else
        return {
          icon: this.iconsStore.alertCircleIcon,
          status: "alert",
          error: "Puste pole!",
        };
    },
    buttonVerification() {
      if (this.titleInput.length < 3) {
        this.isButtonDisabled = true;
      } else {
        this.isButtonDisabled = false;
      }
    },
  },
  watch: {
    titleInput() {
      this.buttonVerification();
      const data = this.formVerification(this.titleInput);
      this.titleIcon = data.icon;
      this.titleClass = data.status;
      this.errors.titleError = data.error;
    },
    descInput() {
      this.lettersCount.descLetters = this.descInput.length;
    },
  },
};
</script>

<style lang="scss" scoped>
#newTaskDetails {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 120px 200px 140px;
  padding: 50px;

  #newTaskHeader {
    grid-row: 1 / 2;
    display: flex;
    align-items: center;
    flex-flow: row nowrap;
    justify-content: space-between;

    #newTaskTitle {
      width: 50%;
    }
  }

  .newTaskItem {
    background: none;
    box-shadow: none;
  }

  #newTaskButtonsContainer {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    margin: 20px;

    .newTaskButton {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 30px;
      height: 30px;
      margin: 5px;
      padding: 0;
      border: 1px solid;
      border-radius: 5px;
      box-shadow: 0 4px 4px #20232a;
      transition: 0.2s ease-in-out;

      &.newTask {
        color: #63cf5a;
        border-color: #63cf5a;

        &:hover {
          color: #419439;
          border-color: #419439;
        }

        &.disabled {
          color: #1e6119;
          border-color: #1e6119;
          cursor: default;
        }
      }

      &.toggleStar {
        color: #dcb364;
        border-color: #dcb364;

        &.disabled {
          color: #644b1d;
          border-color: #644b1d;
        }
      }
    }
  }

  #newTaskDesc {
    grid-row: 2 / 3;

    textarea {
      resize: none;
    }
  }

  #newTaskFooter {
    grid-row: 3 / 4;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-around;

    .newTaskTile {
      display: flex;
      flex-flow: column nowrap;
      justify-content: space-around;
      align-items: center;
      width: 200px;
      height: 100px;
      padding: 10px;
      border: 1px solid #282c35;
      border-radius: 5px;
      box-shadow: 0 4px 4px rgba(0, 0, 0, 0.5);

      h1 {
        letter-spacing: 1px;
        font-weight: normal;
        margin: 0;
      }
    }
  }
}
</style>
