<template>
  <body>
    <div id="backgroundTriangle"></div>
    <HeaderLogo />
    <Notification />
    <header id="headerLeft">
      <Navigation />
      <UserCard />
    </header>
    <main id="mainTasksSection">
      <div id="mainTasksContainer">
        <h1 class="titles">Do zrobienia</h1>
        <section id="tasksSection">
          <div id="tasksHeader">
            <div
              @click="generalStore.activeTaskElement = 'home'"
              :class="{ active: generalStore.activeTaskElement === 'home' }"
              class="tasksHeaderElement pointer"
            >
              <svg-icon type="mdi" :path="iconsStore.mdiHomeOutline"></svg-icon>
              <span>Statystyki</span>
            </div>
            <div
              @click="generalStore.activeTaskElement = 'addTask'"
              :class="{ active: generalStore.activeTaskElement === 'addTask' }"
              class="tasksHeaderElement pointer"
            >
              <svg-icon
                type="mdi"
                :path="iconsStore.mdiPlusCircleOutline"
              ></svg-icon>
              <span>Dodaj zadanie</span>
            </div>
          </div>
          <div id="tasksList">
            <ul id="tasksListScroll">
              <div id="tasksListHeader">
                <span>To Do</span>
                <hr />
                <svg-icon
                  @click="this.isFilterMenuOpen = !this.isFilterMenuOpen"
                  class="taskFilter pointer"
                  type="mdi"
                  :path="filterIcon"
                  style="--sx: 1.25; --sy: 1.25"
                ></svg-icon>
                <div v-if="isFilterMenuOpen" id="tasksListHeaderFilterMenu">
                  <h1>Wybierz status zadania</h1>
                  <div
                    @click="changeFilter('available', filteredStatus)"
                    class="statusBadge filterElement available pointer"
                    :class="{ disabled: filteredStatus !== 'available' }"
                  >
                    <div>
                      <p>Dostępne</p>
                    </div>
                  </div>
                  <div
                    @click="changeFilter('inProgress', filteredStatus)"
                    class="statusBadge filterElement inProgress pointer"
                    :class="{ disabled: filteredStatus !== 'inProgress' }"
                  >
                    <div>
                      <p>W trakcie</p>
                    </div>
                  </div>
                  <div
                    @click="changeFilter('finished', filteredStatus)"
                    class="statusBadge filterElement finished pointer"
                    :class="{ disabled: filteredStatus !== 'finished' }"
                  >
                    <div>
                      <p>Ukończone</p>
                    </div>
                  </div>
                  <div
                    @click="changeFilter('cancelled', filteredStatus)"
                    class="statusBadge filterElement cancelled pointer"
                    :class="{ disabled: filteredStatus !== 'cancelled' }"
                  >
                    <div>
                      <p>Anulowane</p>
                    </div>
                  </div>
                </div>
              </div>
              <div v-if="tasks.length === 0" class="emptyValuesMessage">
                <h1>Brak zadań do wyświetlenia</h1>
              </div>
              <li
                v-for="task in filteredTasks"
                :key="task.id"
                class="taskListElement"
              >
                <router-link :to="`/dashboard/zadania/${task._id}`">
                  <svg-icon
                    v-if="task.status === 'cancelled'"
                    class="taskListElementStatus cancelled"
                    type="mdi"
                    :path="iconsStore.mdiCloseCircleOutline"
                  ></svg-icon>
                  <svg-icon
                    v-else-if="task.status === 'finished'"
                    class="taskListElementStatus finished"
                    type="mdi"
                    :path="iconsStore.successIcon"
                  ></svg-icon>
                  <svg-icon
                    v-else-if="task.status === 'inProgress'"
                    class="taskListElementStatus inProgress"
                    type="mdi"
                    :path="iconsStore.mdiDotsHorizontalCircleOutline"
                  ></svg-icon>
                  <svg-icon
                    v-else
                    class="taskListElementStatus saved"
                    type="mdi"
                    :path="iconsStore.mdiRecordCircleOutline"
                  ></svg-icon>
                  <div class="taskListElementDetails">
                    <span class="taskListElementID">
                      {{ `#${task._id}` }}
                    </span>
                    <h1 class="taskListElementTitle">
                      {{ task.title }}
                    </h1>
                  </div>
                  <svg-icon
                    v-if="task.important"
                    class="taskListElementStar"
                    type="mdi"
                    :path="iconsStore.mdiStar"
                  ></svg-icon>
                </router-link>
              </li>
            </ul>
          </div>
          <section id="taskDetailsSection">
            <TaskDashboard v-if="generalStore.activeTaskElement === 'home'" />
            <AddTask v-if="generalStore.activeTaskElement === 'addTask'" />
          </section>
        </section>
      </div>
    </main>
  </body>
</template>

<script>
import API from "../api";
import svgIcon from "@jamescoyle/vue-icon";

import { useGeneralStore } from "../stores/generalStore";
import { useIconsStore } from "../stores/iconsStore";

import HeaderLogo from "../components/HeaderLogo.vue";
import Notification from "../components/Assets/Notification.vue";
import UserCard from "../components/Assets/UserCard.vue";
import Navigation from "../components/Dashboard/MainNavigation.vue";
import TaskDashboard from "../components/Dashboard/TaskDashboard.vue";
import AddTask from "../components/Dashboard/AddTask.vue";

export default {
  name: "TasksView",
  components: {
    svgIcon,
    HeaderLogo,
    Notification,
    UserCard,
    Navigation,
    TaskDashboard,
    AddTask,
  },
  setup() {
    const generalStore = useGeneralStore();
    const iconsStore = useIconsStore();

    return { generalStore, iconsStore };
  },
  data() {
    return {
      tasks: [],
      isFilterMenuOpen: false,
      isFilterActive: false,
      filterIcon: this.iconsStore.mdiFilterOffOutline,
      filteredStatus: "",
    };
  },
  methods: {
    changeFilter(changeTo, actualStatus) {
      if (changeTo === actualStatus) {
        this.isFilterActive = false;
        this.filteredStatus = "";
      } else {
        this.isFilterActive = true;
        this.filteredStatus = changeTo;
      }
    },
  },
  computed: {
    filteredTasks() {
      return this.tasks.filter((task) => {
        return task.status.includes(this.filteredStatus);
      });
    },
  },
  watch: {
    isFilterActive() {
      if (this.isFilterActive)
        this.filterIcon = this.iconsStore.mdiFilterOutline;
      else this.filterIcon = this.iconsStore.mdiFilterOffOutline;
    },
  },
  async beforeMount() {
    this.tasks = await API.getAllTasks();
  },
};
</script>

<style lang="scss" scoped>
body {
  background-color: #20222a;

  #mainTasksSection {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 75%;

    #mainTasksContainer {
      width: 1200px;
      height: 100%;
      margin: auto;

      #tasksSection {
        display: grid;
        grid-template-areas:
          "tasksHeader taskDetails"
          "tasksList taskDetails";
        grid-template-columns: 350px auto;
        grid-template-rows: 140px auto;
        gap: 10px;
        width: 100%;
        height: 600px;
        border-radius: 5px;
        padding: 20px;
        background: linear-gradient(to bottom right, #20232a, #1a1c23);
        box-shadow: 0 4px 4px #181920;

        #tasksHeader {
          grid-column: 1 / 2;
          grid-row: 1 / 2;
          display: flex;
          flex-flow: column nowrap;
          justify-content: center;
          align-items: center;

          .tasksHeaderElement {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            width: 100%;
            height: 50px;
            margin: 10px;
            color: #d0d0d0;
            font-size: 20px;
            font-weight: bold;
            border: 1px solid #282c35;
            border-radius: 5px;
            box-shadow: 0 4px 4px #181920;
            transition: 0.2s ease-in-out;

            a {
              display: flex;
              flex-flow: row nowrap;
              align-items: center;
              width: 100%;
              height: 100%;
            }

            svg {
              --sx: 1.25 !important;
              --sy: 1.25 !important;
              margin: 10px;
            }

            &.active,
            &:hover {
              box-shadow: 0 4px 4px #181920;
              background-color: #323846;
            }
          }
        }

        #tasksList {
          grid-column: 1 / 2;
          grid-row: 2 / 3;
          padding: 5px;
          border: 1px solid #282c35;
          border-radius: 5px;
          box-shadow: 0 4px 4px #20232a;
          overflow: hidden;

          #tasksListHeader {
            position: relative;
            display: flex;
            flex-flow: row nowrap;
            justify-content: center;
            align-items: center;
            height: 30px;
            padding: 5px;
            color: #3c4454;
            transition: 0.2s ease-in-out;
            user-select: none;

            span {
              width: 50px;
              margin-right: 5px;
              user-select: none;
            }

            hr {
              width: 100%;
              height: 1px;
              border: none;
              background: linear-gradient(to right, #3c4454, #1d2026);
            }

            .taskFilter {
              transition: 0.1s ease-in-out;

              &:hover,
              &:active {
                color: #2e333c;
              }
            }

            #tasksListHeaderFilterMenu {
              position: absolute;
              top: 30px;
              z-index: 1;
              display: flex;
              flex-flow: row wrap;
              gap: 10px;
              justify-content: space-evenly;
              align-items: center;
              width: 100%;
              padding: 10px;
              background: linear-gradient(to bottom right, #20232a, #1a1c23);
              border: 1px solid #282c35;
              border-radius: 5px;
              box-shadow: 0 4px 4px #20232a;

              h1 {
                width: 100%;
                margin: 0;
                color: #d0d0d0;
                font-size: 20px;
              }

              .filterElement {
                transition: 0.2s ease-in-out;

                &.disabled {
                  filter: opacity(0.5);

                  &:hover {
                    filter: opacity(0.75);
                  }
                }
              }
            }
          }

          #tasksListScroll {
            height: 100%;
            margin: 0;
            padding: 5px;
            overflow-y: scroll;

            &::-webkit-scrollbar {
              width: 10px;
            }

            &::-webkit-scrollbar-track {
              background: #282c35;
              border-radius: 5px;
            }

            &::-webkit-scrollbar-thumb {
              background: #105192;
              border-radius: 5px;
            }

            &::-webkit-scrollbar-thumb:hover {
              background: #0e4275;
            }

            .taskListElement {
              width: 100%;
              height: 50px;
              border-radius: 5px;
              margin: 5px 0;
              transition: 0.1s ease-in-out;

              a {
                display: flex;
                flex-flow: row nowrap;
                align-items: center;
                width: 100%;
                height: 100%;
              }

              .taskListElementStatus {
                width: 15%;
                --sx: 1.5 !important;
                --sy: 1.5 !important;

                &.saved {
                  color: #d3d6dd;
                }
                &.inProgress {
                  color: #5886c3;
                }
                &.finished {
                  color: #59e567;
                }
                &.cancelled {
                  color: #c71a1a;
                }
              }

              .taskListElementDetails {
                display: flex;
                flex-flow: column nowrap;
                justify-content: center;
                align-items: flex-start;
                width: 75%;
                overflow: hidden;

                .taskListElementID {
                  margin-left: 5px;
                  color: #999999;
                  font-size: 12px;
                }

                .taskListElementTitle {
                  margin: 0;
                  color: #d0d0d0;
                  font-size: 20px;
                  white-space: nowrap;
                }
              }

              .taskListElementStar {
                width: 10%;
                height: 20px;
                color: #dcb364;
              }

              &.active,
              &:hover {
                background-color: #323846;
              }
            }
          }
        }
      }

      #taskDetailsSection {
        grid-column: 2 / 3;
        grid-row: 1 / 3;
        border-radius: 5px;
        border: 1px solid #282c35;
        box-shadow: 0 4px 4px #181920;
      }
    }
  }
}
</style>
